// material

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
    [theme.breakpoints.up('md')]: {
        display: 'flex'
    }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
    width: '100%',
    maxWidth: 464,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
    minWidth: '100%',
    margin: 'auto ',
    display: 'flex',

    flexDirection: 'column',
    justifyContent: 'center',
    padding: theme.spacing(4, 0)
}));

function AddContactForm({ initValue }) {
    const [showPassword, setShowPassword] = useState();
    let { id } = useParams();
    let history = useHistory();
    const [alertToggleData, setAlertToggelData] = useState({
        isOpen: false,
        message: '',
        type: 'success',
        toggle: false
    });

    const insertApi = val => {
        console.log(val)
        fetch(`${HOST}user/add`, {
            method: 'POST',
            body: JSON.stringify(val),
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(respose => respose.json())
            .then(doc => {
                console.log(doc)
                handleReset();
                setAlertToggelData((pre) => ({ ...pre, message: doc.message, isOpen: true, toggle: !alertToggleData.toggle }));
            })
            .catch(err => console.log(err))
    }

    const editApi = val => {
        console.log(val)
        fetch(`${HOST}user/upd`, {
            method: 'PUT',
            body: JSON.stringify(val),
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(respose => respose.json())
            .then(doc => {
                console.log(doc)
                handleReset();
                history.push('/admin/userlist');
            })
            .catch(err => console.log(err))
    }

    const RegisterSchema = Yup.object().shape({
        name: Yup.string()
            .min(2, 'Too Short!')
            .max(50, 'Too Long!')
            .required('First name required'),
        role: Yup.string().min(2, 'Too Short').required('this is required'),
        email: Yup.string().email('Email must be a valid email address').required('Email is required'),
        active: Yup.string().required('this feild required'),
        password: Yup.string().min(2, 'Too Short!').required('New Password Required'),
        confirm_password: Yup.string().oneOf([Yup.ref('password'), null], 'Passwords must match').required('Confirm Password is required')
    });

    console.log(initValue);

    let formik = useFormik({

        initialValues: {
            name: initValue.name || '',
            email: initValue.email || '',
            role: initValue.role || '',
            active: initValue.active || '',
            password: initValue.password || '',
            confirm_password: initValue.password || '',
        },
        validationSchema: RegisterSchema,
        onSubmit: (value) => {
            const val = value;
            console.log(val);
            if (id !== undefined) {
                editApi({
                    // title: val.category_title === '' ? initValue.categoryTitle : val.category_title,
                    name: val.name,
                    email: val.email,
                    active: val.active,
                    role: val.role,
                    password: val.password,
                    userId: id
                })
            } else {
                insertApi({
                    name: val.name,
                    email: val.email,
                    role: val.role,
                    active: val.active,
                    password: val.password
                })
            }
        }
    });



    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    }

    const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;

    return (
        <FormikProvider value={formik}>
            <CustomizedSnackbars isOpen={alertToggleData.isOpen} role={alertToggleData.message} toggle={alertToggleData.toggle} />

            <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
                <Stack spacing={3}>
                    {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
                    <TextField
                        fullWidth
                        label={"Name"}
                        {...getFieldProps('name')}
                        error={Boolean(touched.name && errors.name)}
                        helperText={touched.name && errors.name}
                        placeHolder={'Name'}
                    />

                    <TextField
                        fullWidth
                        label={"Email"}
                        {...getFieldProps('email')}
                        error={Boolean(touched.email && errors.email)}
                        helperText={touched.email && errors.email}
                        placeHolder={'Email'}
                    />

                    <Stack justifyContent="space-between" direction={{ xs: 'column', sm: 'row' }} spacing={3}>

                        <FormControl fullWidth error={Boolean(touched.active && errors.active)} >
                            <InputLabel id="demo-simple-select-label">active</InputLabel>

                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                label="active"
                                {...getFieldProps('active')}
                            >
                                <MenuItem value={'true'}>Active</MenuItem>
                                <MenuItem value={'false'}>Deactive</MenuItem>
                            </Select>
                            {Boolean(touched.active && errors.active) && <FormHelperText>{touched.active && errors.active}</FormHelperText>}


                        </FormControl>

                        <FormControl fullWidth error={Boolean(touched.role && errors.role)} >
                            <InputLabel id="demo-simple-select-label">Role</InputLabel>

                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                label="Role"
                                {...getFieldProps('role')}
                            >
                                <MenuItem value={'admin'}>Admin</MenuItem>
                                <MenuItem value={'subAdmin'}>Subadmin</MenuItem>

                            </Select>
                            {Boolean(touched.role && errors.role) && <FormHelperText>{touched.role && errors.role}</FormHelperText>}
                        </FormControl>
                    </Stack>
                    <FormControl fullWidth variant="outlined">
                        <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            type={showPassword ? 'text' : 'password'}
                            label={"Password"}
                            {...getFieldProps('password')}
                            error={Boolean(touched.password && errors.password)}
                            helperText={touched.password && errors.password}
                            // onChange={handleChange('password')}
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        // onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {showPassword ? <VisibilityOff /> : <Visibility />}
                                    </IconButton>
                                </InputAdornment>
                            }
                            label="Password"
                        />
                    </FormControl>
                    <FormControl fullWidth variant="outlined">
                        <InputLabel htmlFor="outlined-adornment-password">Confirm Password</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            type={showPassword ? 'text' : 'password'}
                            label={"Confirm Password"}
                            {...getFieldProps('confirm_password')}
                            error={Boolean(touched.confirm_password && errors.confirm_password)}
                            helperText={touched.confirm_password && errors.confirm_password}
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        // onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {showPassword ? <VisibilityOff /> : <Visibility />}
                                    </IconButton>
                                </InputAdornment>
                            }
                        />
                    </FormControl>

                    <LoadingButton
                        fullWidth
                        size="large"
                        type="submit"
                        variant="contained"
                        loading={isSubmitting}
                    >
                        {id ? "Update" : "Submit"}
                    </LoadingButton>
                </Stack>
            </Form>
        </FormikProvider>
    );
}

export default function AddContact() {
    let { id } = useParams();
    console.log(id);
    const [change, setChange] = useState(false);
    const [initValue, setInitValue] = useState({
    });
    if (id !== undefined) {
        useEffect(() => {
            fetch(`${HOST}user/find/${id}`)
                .then(res => res.json())
                .then(res => {
                    if (res) {
                        setInitValue(res);
                        console.log(res)
                    }
                }).then(
                    setTimeout(() => setChange(true), 200)
                    // setChange(true)
                )
                .catch(err => console.log(err))

        }, [id])
    } else {
        setTimeout(() => setChange(true), 0);
    }
    return (
        <Page title="User | Minimal-UI">
            <Container>
                <Box pt={12}></Box>
                <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
                    <Typography variant="h4" gutterBottom>
                        {id ? "Edit User" : "Add User"}
                    </Typography>
                </Stack>
                <Card >
                    <RootStyle>
                        <ContentStyle>
                            <Container>
                                {change && <AddContactForm initValue={initValue} />}
                            </Container>
                        </ContentStyle>
                    </RootStyle>
                </Card>
            </Container>
        </Page>
    );
}
