import React, { useEffect, useState } from 'react'
import HOST from '../../../components/Env';

export const isAdminA = (val) => {
  if(val === true){
    return true;
  }else{
    return false
  }
}
const useUser = () => {
    const [userInfo, setUserInfo] = useState([]);
    const [isAdmin, setIsAdmin] = useState(false);
        useEffect(()=>{
            fetch(`${HOST}user/find`,{
              method: 'GET',
              headers:{
                'Authorization': `bearer ${localStorage.getItem('token')}`
              }
            }).then(res=>res.json())
            .then(res=>{
              setUserInfo(res);
              if(res.role === 'admin'){
                  setIsAdmin(true);
                  isAdminA(true);
              }else{
                setIsAdmin(false);
                isAdminA(false);
              }
              console.log(res);
            }).catch(err=>console.log(err))
            return ()=>isAdminA(false);
          },[]);

    return {
        userInfo,
        isAdmin
    }   
}
export const isAdmin = isAdminA();

export default useUser
